.contact {
  padding: 3rem;
  height: 65vh;
  background-color: rgb(30,30,30);
}
.contact-header {
  text-align: center;
}

.description {
  padding: 1rem;
  display: table;
  font-size: medium;
  font-weight: 300;
  margin: 0 auto;
}

.contact-form {
  margin: 1rem;
  display: flex;
  margin: auto;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 90vw;
  max-width: 60rem;
}

.name,
.email,
.message {
  color: white;
  font-size: medium;
  width: 100%;
  max-width: 40rem;
  margin: 0.5rem;
  padding: 0.5rem 1rem;
  border: none;
  border-radius: 0.5rem;
  background: rgb(55, 55, 55);
}

.submit-btn {
  background: white;
  border: none;
  border-radius: 0.5rem;
  margin: 2rem;
  padding: 0.75rem 3.5rem;
  cursor: pointer;
}

.email-sent-message {
  text-align: center;
  font-size: 2.5rem;
  margin-top: 20%;
  opacity: 1;
  transform: translateY(20px);
  transition: transform 1s;
  animation: fadeInUp 1s ease-out;
}
